.section {
  box-sizing: border-box;
  box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.45);
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  border-radius: 1.875rem 0;
  padding: 1.25rem;
  background-color: #ecebeb;
  position: fixed;
  max-width: 90%;
  max-height: 90%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 31.25rem;
  height: 25rem;
  font-size: 1rem;
  user-select: none;
}
.section .cross {
  position: absolute;
  right: 1.25em;
  width: 1em;
  height: 1em;
  cursor: pointer;
  transition: 0.5s;
}
.section .cross:hover {
  fill: #d45287;
}
.section .header h2 {
  color: #d45287;
  font-size: 1.25em;
  text-align: center;
  margin: 0;
  padding-bottom: 1em;
}
.section .scrollable {
  flex-grow: 2;
  overflow-y: scroll;
}
.section .scrollable p {
  color: #000000;
  margin: 0;
  padding: 0.3125em 0;
}
.section .footer {
  height: 1.4375em;
  padding-top: 1.25em;
}
.section .footer .arrow {
  margin: 0 auto;
  height: 1.25em;
  width: 1.25em;
  cursor: pointer;
  transition: 0.5s;
}
.section .footer .arrow.left {
  transform: rotate(180deg);
}
.section .footer .arrow:hover {
  fill: #d45287;
}
.section a {
  color: #d45287;
  text-decoration: underline;
}
