.checkbox-label {
  display: flex;
  align-items: center;
  height: calc(1.3em + 2px);
  margin-bottom: 0.5em;
}
.checkbox-label input[type="checkbox"] {
  opacity: 0;
  position: fixed;
  margin: 0;
  padding: 0;
}
.checkbox-label span {
  position: relative;
  padding-left: 2em;
  height: 100%;
  line-height: 1.5;
}
.checked.checkbox-label span::after {
  content: "";
}
.checkbox-label span::before,
.checkbox-label span::after {
  position: absolute;
  display: inline-block;
}
.checkbox-label span::before {
  content: "";
  height: 1.3em;
  width: 1.3em;
  border: 1px solid;
  left: 0;
  border-radius: 50em;
}
.checkbox-label span::before:hover {
  color: red;
}
.checkbox-label span::after {
  content: none;
  height: 0.375em;
  width: 0.5625em;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-45deg);
  left: calc((1.3em - 0.5625em) / 2);
  top: calc(1.3em / 4);
}
