@font-face {
  font-family: "dekiru";
  src: url(Dekiru.ttf) format("truetype");
  font-weight: normal;
  font-style: normal;
}
html {
  font: 16px "dekiru", corbel, sans-serif;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
body {
  background: #ecebeb no-repeat center / cover url(images/background.jpg);
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
#root {
  background-color: rgba(236, 235, 235, 0.4);
  width: 100%;
  height: 100%;
}
button {
  font: 1.25rem "dekiru", corbel, sans-serif;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}
@media (orientation: landscape) and (max-height: 335px) {
  button {
    font-size: 1.1rem;
  }
}
button:focus {
  outline: none;
}
a {
  color: #000000;
  text-decoration: none;
}
@media (hover: hover) {
  a:hover {
    color: #d45287;
    text-decoration: underline;
  }
}
svg text {
  user-select: none;
}
