.side-menu-container {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  pointer-events: none;
}
.side-menu {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  display: flex;
  flex-flow: row nowrap;
  align-items: stretch;
  justify-content: flex-start;
  transition: 0.2s ease-in-out;
  pointer-events: all;
}
.hidden .side-menu {
  transform: translateX(-100%);
}
.side-menu .menu {
  background-color: rgba(236, 235, 235, 0.95);
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: flex-start;
  padding: 1.25rem;
  box-shadow: 0 0 1.25rem rgba(0, 0, 0, 0.45);
}
.side-menu .menu .side-menu-logo {
  width: 16.25em;
  height: 6.125em;
  font-size: 1rem;
}
@media (orientation: landscape) and (max-height: 335px) {
  .side-menu .menu .side-menu-logo {
    font-size: 0.8rem;
  }
}
@media (orientation: portrait) and (max-width: 374px) {
  .side-menu .menu .side-menu-logo {
    font-size: calc(0.26rem + 2.5vw);
  }
}
.side-menu .menu .side-menu-logo text {
  font: bold 5em "dekiru", corbel, sans-serif;
  dominant-baseline: text-before-edge;
}
.side-menu .menu .buttons-wrapper {
  display: flex;
  flex-flow: column nowrap;
  margin: 1.875rem 0;
}
@media (orientation: landscape) and (max-height: 450px) {
  .side-menu .menu {
    justify-content: space-between;
  }
  .side-menu .menu .buttons-wrapper {
    margin: 0;
  }
}
.side-menu .side-menu-button {
  position: absolute;
  left: 110%;
  top: 1.875em;
  height: 2rem;
  fill: black;
  font-size: 1rem;
}
.side-menu .side-menu-button svg {
  width: 2em;
  height: 2em;
}
@media (hover: hover) {
  .side-menu .side-menu-button :hover {
    cursor: pointer;
    fill: #d45287;
  }
}
@media (orientation: portrait) and (max-width: 400px) {
  .side-menu .side-menu-button {
    font-size: 0.8rem;
  }
}
@media (orientation: landscape) and (max-height: 450px) {
  .side-menu .side-menu-button {
    font-size: 0.8rem;
  }
}
.side-menu-overlay {
  width: 100%;
  height: 100%;
  pointer-events: all;
}
.opened .side-menu-overlay {
  background-color: rgba(236, 235, 235, 0.5);
}
.hidden .side-menu-overlay {
  display: none;
}
