.lobby {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: visible;
  opacity: 1;
}
.lobby.hidden {
  visibility: hidden;
  opacity: 0;
}
.lobby .lobby-wrapper {
  height: 25rem;
  max-height: 90%;
  width: 100%;
  max-width: 22.5rem;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: space-between;
}
