.menu-button {
  background-color: #000000;
  border: 0;
  color: #ffffff;
  border-radius: 0 0.6em;
  min-width: 5.5em;
  min-height: 1.9em;
  padding: 0.35em;
  margin: 0.5em 0;
  transition: 0.5s;
}
@media (orientation: landscape) and (max-height: 400px) {
  .menu-button {
    margin: 0.3em 0;
  }
}
.menu-button.reversed {
  border-radius: 0.6em 0;
}
.menu-button.disabled {
  pointer-events: none;
  background-color: #e2e1e1;
  color: #000000;
}
.menu-button:hover {
  background-color: #d45287;
}
.game-button {
  background-color: #000000;
  border: 0;
  color: #ffffff;
  border-radius: 2.625em;
  height: 4.1875em;
  width: 4.125em;
  padding: 0;
  transition: 0.5s;
  display: flex;
  justify-content: center;
}
.game-button.small {
  height: 2.5em;
  width: 2.5em;
  font-size: 1em;
}
.game-button:hover,
.game-button.selected {
  background-color: #d45287;
}
