textarea {
  text-align: center;
  padding: 0;
  resize: none;
  vertical-align: middle;
  box-sizing: border-box;
  border: none;
  width: 100%;
  height: 100%;
}
textarea:focus {
  outline: 0;
}
.pencil {
  font-size: 1.4em;
  letter-spacing: 0;
  line-height: 1.5625em;
  background-color: #ffffff;
}
.readOnly {
  font-size: 1.4em;
  letter-spacing: 0;
  line-height: 1.5625em;
  background-color: #e2e1e1;
  cursor: default;
}
.notes {
  font-size: 0.7em;
  letter-spacing: 0.3em;
  line-height: 1;
  background-color: #ffffff;
}
.highlight {
  background-color: #f8c9dc;
}
.error {
  line-height: calc(1.5625em - (4px * 2));
  border: 4px solid #c40202;
}
.disabled {
  cursor: default;
}
